import React from 'react';
import {get} from "lodash";
import {Document, Page, Text, View, StyleSheet, Font} from "@react-pdf/renderer";
import times from "../../assets/fonts/times/times.ttf"
import timesBold from "../../assets/fonts/times/Times New Roman Bold.ttf"
import dayjs from "dayjs";

Font.register({
    family: "Times-R",
    src: times
});
Font.register({
    family: "Times-B",
    src: timesBold
});
const styles = StyleSheet.create({
    page: {
        backgroundColor: 'white',
        flex: 1,
        flexDirection: 'column',
        padding: '20 20 30 30',
    },
    row: {
        flexDirection: 'row',
        alignItems: 'flex-start',
    },
    column: {
        flexDirection: 'column',
        alignItems: 'flex-start',
    },
    section: {
        flexDirection: 'row',
        alignItems: 'flex-start',
        marginBottom: '5px'
    },
    text: {
        xxs: {fontSize: 8},
        xs: {fontSize: 10},
        sm: {fontSize: 12},
        base: {fontSize: 14},
        lg: {fontSize: 16},
        xl: {fontSize: 18},
        xxl: {fontSize: 24},
    },
    font: {
        times: {
            fontFamily: "Times-R",
        },
        timesB: {
            fontFamily: "Times-B",
        }
    },
    box: {
        border: '1px solid #000',
        padding: '1.5px 3px',
        marginRight: 5
    },
    checkbox: {
        border: '1px solid #000',
        width: 16,
        height: 16,
        marginRight: 3,
        checked: {
            backgroundColor: '#000'
        }
    },
    flex: {
        between: {
            justifyContent: 'space-between'
        },
        center: {
            justifyContent: 'center'
        },
        alignBottom: {
            alignItems: 'center'
        }
    },
    borderBottom: {
        borderBottom: '1px solid #000'
    },
    width: {
        25: {
            width: '25%'
        },
        40: {
            width: '40%'
        },
        50: {
            width: '50%'
        },
        60: {
            width: '60%'
        }
    }
});
